.todoContainer{
    width: 60%;
    border: 2px solid black;
    border-radius: 8px;
    background-color: #ebeef052;
    display: flex;
    flex-wrap: calc(100 / 2);
    padding: 50px 10px;
    margin: 80px auto;
}
.texto{
    font-size: 25px;
    font-weight: bold;
    text-align: center;
}
.todoCreateForm{
    width: 50%;
}
.todoInput{
    width: 90%;
    padding: 5px;
    margin: 10px;
}
.buttonCreate{
    padding: 10px;
    margin: 10px;
    border: none;
    border-radius: 6px;
    background-color: #fb5607;
}
.buttonCreate:hover{
    background-color: #cc4c11;
    transition: 0.2 ease-in-out;
}




.todasTareas{
    width: 50%;
    max-height: 370px;
    overflow-y: scroll;
}
.todo{
    border-radius: 8px;
    background-color: #cc4c117c;
}
.todoInfo{
    padding: 10px;
    margin: 5px;
    background-color: white;
}
.todoUpdateForm{
    background-color: #9ef01aa2;
    
}
.todoInput{
   
}
.button{
    border: none;
    padding: 8px 30px;
    margin: 5px 10px;
    border: 1px solid #222;
    background-color: #fff;
}
.button:hover{
    background-color: #ffb703;
    transition: 0.2s ease-in-out;
}
.btn{
    background-color: #cc4c11;
    padding: 8px 30px;
    border: none;
    margin: 10px;
}
.btn-editar{
    background-color: #5e60ce;
}

.btn-eliminar{
    background-color: #ef233c;
}
.btn-editar:hover{
    background-color: #5859b6;
    transition: 0.2s ease-out;
    font-size: 15px;
}

.btn-eliminar:hover{
    background-color: #d62e41;transition: 0.2s ease-out;
    transition: 0.2s ease-out;
    font-size: 15px;
}